uto;height:auto;margin-top:50px;margin-bottom:50px}input{margin-bottom:20px}input[type="submit"]{margin-left:0px}.content{width:90%}form{flex-direction:column}.illustration>img{width:100%;height:100%}.footer{margin-top:100px}h2{font-size:1.5em}h2>span{font-size:0.9em}h3{text-align:center;font-size:0.9em}.error{padding-left:0px}span.error{font-size:13px;text-align:center;margin-bottom:20px}}

